<template>
  <div id="app">
    <div class="container-fluid">
      <Header/>
      <hr style="border: none">
      <div class="row mt-5 student-reg">
        <div class="col-6 pl-0"><img src="../../../assets/img/ly_reg.png" height="560"></div>
        <div class="col-6 text-center pr-7" >
          <div class="main-title">助教注册</div>
          <div class="row mt-5">
            <div class="center-block" style="width:300px;">
              <div class="row text-center">
                <el-form class="mt-3" ref="form" :model="form" :rules="rules"  :show-message="true" >
                  <el-form-item prop="phone">
                    <el-input placeholder="请输入11位手机号" v-model="form.phone" maxlength="11"></el-input>
                  </el-form-item>
                  <el-form-item  prop="verify">
                    <el-input placeholder="验证码" v-model="form.verify" maxlength="6" style="width: 210px;"></el-input>
                    <el-popover
                      placement="right"
                      trigger="click">
                      <slide-verify :l="42"
                                    :r="10"
                                    :w="310"
                                    :h="155"
                                    @success="onSuccess"
                                    @fail="onFail"
                                    @refresh="onRefresh"
                                    @changeNum = "change"
                                    :sliderText="'请向右滑动'"
                                    :setNum = "60"
                      ></slide-verify>
                      <div>{{msg}}</div>
                      <el-button slot="reference" v-if="!canGetVerify" disabled class="verifycode el-button">{{verifyCodeText}}</el-button>
                      <el-button slot="reference" v-if="canGetVerify" :disabled="countdownshow" class="verifycode el-button"><span v-show="!countdownshow">{{verifyCodeText}}</span><span v-show="countdownshow">{{countdown}}s</span></el-button>
                    </el-popover>
                  </el-form-item>
                  <el-form-item  prop="pwd">
                    <el-input placeholder="设置密码" v-model="form.pwd" show-password maxlength="20"></el-input>
                  </el-form-item>
                  <div class="row" style="width: 330px;margin: unset" v-show="this.form.pwd != ''">
                    <div class="pull-right" style="display: flex">
                      <div :class="level != 0?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-right: 5px;margin-bottom: 16px;"></div>
                      <div :class="level ==2 || level == 3?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-right: 5px;margin-bottom: 16px;"></div>
                      <div :class="level == 3?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-bottom: 16px; margin-right: 10px;"></div>
                    </div>
                  </div>
                  <el-form-item prop="pwd">
                    <el-input placeholder="确认密码" v-model="form.pwd2" show-password maxlength="20"></el-input>
                  </el-form-item>
                  <el-form-item prop="inviteCode">
                    <el-input placeholder="输入邀请码" v-model="form.inviteCode" maxlength="11"></el-input>
                  </el-form-item>
                  <!--<el-form-item prop="read">-->
                  <!--<el-checkbox v-model="form.read"><span class="clause">我已阅读同意</span></el-checkbox>-->
                  <!--<a class="mr-3 clause" href="" style="color: #6612CF;">《励英网服务条款》</a>-->
                  <!--</el-form-item>-->
                </el-form>
                <el-checkbox v-model="form.read"><span class="clause">我已阅读同意</span></el-checkbox>
                <a class="mr-3 clause" href="lying-clause.html" style="color: #6612CF;">《励英网服务条款》</a>
              </div>
              <div class="row mt-2 mb-3">
                <div class="btn btn-block reg-btn" @click="register('form')" >注册</div>
              </div>
              <div class="row">
                <div class="text-center clause">
                  <span>已有账号？</span>
                  <a href="login.html" style="color: #6612CF;">去登录</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer/>
    </div>
  </div>
</template>

<script>
import Header from '../../../components/reg/Header.vue'
import Footer from '../../../components/reg/Footer.vue'
export default {
  name: 'app',
  components: {
    Header,
    Footer
  },
  data () {
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号'))
      } else {
        if (this.form.phone !== '') {
          var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
          if (!reg.test(this.form.phone)) {
            callback(new Error('手机号格式不正确'))
          }
        }
        callback()
      }
    }
    var filterPhone = (rule, value, callback) => {
      this.form.phone = value.replace(/[^\d]/g, '')
      callback()
    }
    var filterInviteCode = (rule, value, callback) => {
      this.form.inviteCode = value.replace(/[^\d]/g, '')
      callback()
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.form.pwd2 !== '') {
          if (this.form.pwd2 !== this.form.pwd) {
            callback(new Error('两次密码输入必须一致！'))
          }
        }
        callback()
      }
    }
    var filterPass = (rule, value, callback) => {
      var patrn = /^(\w){6,20}$/
      if (!patrn.exec(value)) {
        callback(new Error('只能输入6-20个字母、数字、下划线 '))
      }
      callback()
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.pwd) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var filterVerify = (rule, value, callback) => {
      this.form.verify = value.replace(/[^\d]/g, '')
      callback()
    }
    return {
      msg: '',
      level: '', // 密码强度等级
      verifyCodeText: '获取验证码', // 验证码按钮显示内容
      interval: null, // 验证码码定时器
      countdown: '', // 验证码倒计时秒数
      countdownshow: false, // 验证码倒计时秒数显示标志
      canGetVerify: false,
      form: {
        phone: '',
        verify: '',
        pwd: '',
        pwd2: '',
        inviteCode: '',
        read: false,
        regValue: ''
      },
      rules: {
        phone: [
          { min: 11, max: 11, message: '手机号长度应为11位', trigger: 'blur' },
          { validator: validatePhone, trigger: 'blur' },
          { validator: filterPhone, trigger: 'change' }
        ],
        verify: [
          { validator: filterVerify, trigger: 'change' },
          { required: true, message: '请输入短信验证码', trigger: 'blur' }
        ],
        pwd: [
          { validator: validatePass, trigger: 'blur' },
          { validator: filterPass, trigger: 'change' }
        ],
        pwd2: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        inviteCode: [
          { required: true, message: '请输入邀请码', trigger: 'blur' },
          { validator: filterInviteCode, trigger: 'change' }
        ],
        read: [
          { required: true, message: '请选择', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
  },
  watch: {
    'form.pwd': {
      deep: true,
      handler: function () {
        var pwd = this.form.pwd
        var reg1 = /\d/
        var reg2 = /[a-zA-Z]/
        var reg3 = /\W/
        var level = 0
        if (reg1.test(pwd)) {
          // 密码等级增加
          level++
        }
        if (reg2.test(pwd)) {
          // 密码等级增加
          level++
        }
        if (reg3.test(pwd)) {
          // 密码等级增加
          level++
        }
        this.level = level
      }
    },
    'form.phone': {
      deep: true,
      handler: function () {
        var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
        if (this.form.phone === null || this.form.phone === undefined || !reg.test(this.form.phone)) {
          this.canGetVerify = false
        } else {
          this.canGetVerify = true
        }
      }
    }
  },
  methods: {
    onSuccess () {
      let self = this
      this.msg = '验证成功,请注意查收短信。'
      this.countdown = 60
      this.countdownshow = true
      // 开始计时
      this.interval = setInterval(() => {
        this.countdown--
        if (this.countdown <= 0) {
          this.verifyCodeText = '重新发送'
          this.countdownshow = false
          clearInterval(this.interval)
        }
      }, 1000)
      this.$http.post('/user/vcfraudprevent', {}, res => {
        if (res.success) {
          self.$http.post('/user/sendsmsverificationcode', {
            'phone': self.form.phone,
            'msgType': 'register',
            'sign': res.data.sign
          }, res => {
            if (res.success) {
            } else {
              self.$alert('提示', res.errorMsg)
              this.verifyCodeText = '重新发送'
              this.countdownshow = false
              clearInterval(this.interval)
            }
          })
        } else {
        }
      })
    },
    onFail () {
      this.msg = ''
    },
    onRefresh () {
      console.log('ok----')
    },
    change (num) {
      console.log('num', num)
    },
    register (formName) {
      let me = this
      if (this.form.read === false) {
        alert('请选择同意条款！')
        return false
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post('/user/register', {
            'userCellphone': this.form.phone,
            'userPassword': this.form.pwd,
            'invitationCode': this.form.inviteCode,
            'smsVerificationCode': this.form.verify,
            'userType': '1'
          }, res => {
            if (res.success) {
              me.$alert('注册成功，跳转到登录页', '提示', {
                callback: action => {
                  window.location = 'login.html'
                }
              })
            } else {
              me.$alert(res.errorMsg, '提示', {
                confirmButtonText: '确定'
              })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
  @import "../../../assets/css/bootstrap.css";
  @import "../../../assets/css/theme.css";
  @import "../../../assets/css/text.css";
  .verifycode{
    width: 105px;
    float: right;
    margin-left: 5px;
    padding: 12px 15px!important;
  }
  .level-color{
    background:rgba(102,18,207,1)!important;
  }
  .student-reg{
    width:1000px;
    height:560px;
    margin:0 auto;
    background:rgba(255,255,255,1);
    box-shadow:0px 2px 15px 0px rgba(0, 0, 0, 0.3);
    border-radius:8px;
  }
  .main-title{
    font-size:30px;
    font-family:PingFang-SC-Bold;
    font-weight:bold;
    color:rgba(102,18,207,1);
    text-align: center;
    margin-top: 29px;
  }
  .clause{
    font-size:12px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(153,153,153,1);
  }
  .reg-btn{
    width:330px;
    background:rgba(102,18,207,1);
    box-shadow:0px 4px 15px 0px rgba(102, 207, 18, 0.35);
    border-radius:23px;
    color: white;
    height: 46px;
    line-height: 46px;
    padding: unset;
  }
</style>
<style>
  .el-input__inner{
  }
  .el-input__inner:focus{
    border-color: #6612CF!important;
  }
  .el-button, .el-button:active, .el-button:focus, .el-button:hover{
    background-color: #6612cf!important;
    color: white!important;
    border-color: unset!important;
  }
  .el-button.is-disabled, .el-button.is-disabled:focus, .el-button.is-disabled:hover {
    cursor: not-allowed;
    background-color: #6612cf!important;
    color: white!important;
    border-color: unset!important;
  }
  .el-checkbox, .el-checkbox__inner{
    border-radius: 10px!important;
    margin-right: 0px!important;
  }
  .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #32C25A!important;
    border-color: #32C25A!important;
  }
  .el-checkbox__inner:hover{
    border-color: #32C25A!important;
  }
</style>
